    //Ë∞ÉÁî®WikiÁôæÁßëAPI
    async searchWiki(keyword) {
      try {
        const response = await axios.get("https://zh.wikipedia.org/w/api.php", {
          params: {
            format: "json",
            action: "query",
            generator: "search",
            gsrnamespace: 0,
            gsrlimit: 5,
            prop: "pageimages|extracts",
            pilimit: "max",
            exintro: true,
            explaintext: true,
            exsentences: 1,
            exlimit: "max",
            origin: "*",
            gsrsearch: keyword,
          },
        });
        const pages = response.data.query.pages;
        const wikiPages = [];
        for (let pageId in pages) {
          const page = pages[pageId];
          const wikiPage = {
            title: page.title,
            thumbnail: page.thumbnail,
            extract: page.extract,
            url: ""
          };
          // Â∞ÜtitleÊãºÊé•ÊàêËÆøÈóÆ‰∏≠ÊñáÁª¥Âü∫ÁôæÁßëÁöÑÈìæÊé•
          wikiPage.url = "http://zh.wikipedia.org/wiki/" + encodeURIComponent(page.title);
          wikiPages.push(wikiPage);
        }
        this.WikiPage = wikiPages;
        console.log(this.WikiPage);
      } catch (error) {
        console.error("Error:", error);
      }
    },
